.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root {
    --color-primary : #1e3a8a;
    --color-accest : #3b82f6;
    --color-bg : #eceff4;
    --color-text : #111111;
    --color-white : #ffffff;
}

body {
    margin: 0;
    padding: 0;
    background-color: var(--color-bg);
    color: var(--color-text);
    font-family: Arial sans-serif;
    overflow-x: hidden;
}

.container_button button {
    cursor: pointer;
    border: none;
    font-weight:500;
}

header {
    background-image: url('./Checkout.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 20vh;
    position: sticky;
    width: 100%;
    top: 12%;
    z-index: 10;
    padding: 1% 3% 1% 3%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
    transition: height 2s ease-in;
} 

header:hover {
    height: 80vh;
}

header h1,h2,strong {
    text-align: center;
    color: white;
}

header h1 {
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color:white;
    width: 24%;
    margin-left: 33%;
    padding: 0.5%;
    position: absolute;
    /* background-color: red; */
    bottom: 30%;
}

header p {
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color:white;
    position: absolute;
    top: 50%;
    padding: 0.5%;
    margin-left: 33%;
}

h1,h2 {
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

header button {
    display: block;
    margin-left: 40%;
    width: 15%;
    cursor: pointer;
    padding: 1%;
    margin-top: 6%;
    border: none;
}

header button a {
    color: white;
    text-decoration: none;
}

header #hover-message {
    display: none;
}

header:hover .default-text{
    display: none;
}

header:hover #hover-message{
    display: block;
}

