    color:  blue;
}

.color-darkbule {
    color: darkblue;
}

.color-gray {
    color: #eceff4;
} */

#home {
    width: 94%;
}

main {
    padding: 0 3%;
    margin-bottom: 5%;
}

#main {
    padding: 5% 0;
}

.logo span { 
    display: inline-block;
    position: absolute;
    top: 5.3%;
    left: 14%;
    padding: 1%;
    padding-top:1%;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color: white;
    font-size: large;
}

.logo img {
    width: 7%;
    margin-left: 5%;
    cursor: pointer;
    margin-top: 0.3%;
    vertical-align:middle;
    background-color: inherit;
    position: absolute;
    left: 1%;
    z-index: 1000;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,1),0 17px 50px 0 rgba(0,0,0,1); 
}

.navbar {
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: white;
}

.top_navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-bottom: 4.2%;
    background-color: var(--color-primary);
    width: 100%;
    display: flex;
    height: auto;  
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);   
}

nav .top_navbar li:first-child {
    margin: 1.5% 0 0 26.5%;
}

.top_navbar li {
    display: inline-block;
    text-align: center;
    height: 0.1%;
    width: 10%;
    margin: 1.5% 0 0.1% 1%;
}

nav .top_navbar li:last-child {
    width: 12%;
}

.top_navbar li a {
    display: inline-block;
    text-decoration: none;
    color: white;
    font-size: larger;
    width: 100%;
    text-align: center;
    margin: 2%;
    /* background-color: indianred; */
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

#order_history a {
    width: auto;
    overflow: hidden;
    padding: 10%;
    position: relative;
    color: white;
}

.history a {
    display: block;
    width: 70%;
}

.nav-link[href="index.html"] {
    color: #333;
}

.nav-link[href="order_history.html"] {
    color: #333;
}

.nav-link[href="checkout.html"] {
    color: #333;
}

.nav-link[href="contact.html"] {
    color: #333;
}

nav .dropdown {
    position: relative;
    cursor: pointer;
    margin-top: 1.6%;
    margin-left: 1%;
    width: 5%;
    height: 0%;
    z-index: 999;
    padding: 1.1%;
}

nav .dropdown span {
    display: block;
    text-align: center;
}

nav .dropdown a {
    display: block;
    text-decoration: none;
    text-align: center;
    color: white;
    padding: 5%;
    width: 100%;
    position: absolute;
    bottom: 0.1%;
    top: 0.2%;
    right: 0.1%;
}

nav .dropdown_content {
    display: none;
    position: absolute;
    left: 0.1%;
    width: 143%;
    background-color: var(--color-white);
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,1);
    margin: 1% auto;
    margin-top: 20%;
    z-index: 1;
}

nav .dropdown_content li {
    width: 65%;
    display: block;
    margin: 5% auto;
    padding: 12%;
    padding-bottom: 20%;
    position: relative;
    border-bottom: 4px solid var(--color-primary);
}

/* .dropdown_content li:last-child {
    margin-top: 1%;
} */

nav .dropdown_content li a {
    width: 82%;
    position: absolute;
    bottom: 80%;
    display: block;
    top: 5%;
    right: 1%;
    height: 50%;
    padding-bottom: 8%;
}

nav .dropdown_content li:first-child {
    margin: 0;
    margin-left: 8%;
}

nav .dropdown_content li:last-child {
    color: white;
    width: 65%;
    margin: 0;
    margin-left: 5%;
    margin-bottom: 3%;
}

nav .dropdown:hover .dropdown_content {
    display: flex;
    flex-direction: column;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,1),0 17px 50px 0 rgba(0,0,0,1); 
}

main {
    display: flex; 
    width:100%; 
    padding-bottom: 8%;
}

.cart_checkout .main {
    width: 90%;
    display: flex;
    margin-top: 2%;
}

.parent {
    width: 100%;
    margin: 2% auto;
    padding: 2% 10%;
    margin-left: 2%;
}

.parent:first-child {
  padding-top: 5.5%;
}

#parent {
  width: 130%;
}

.parent h1 {
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color: white;
    text-align: center;
    padding: 1%;
}

.cart_button button {
  margin: 2%;
  padding: 2%;
  background-color: var(--color-primary);
  cursor: pointer;
  color: white;
}

.cart_button button:last-child {
    animation: pulse 2s infinite;
    color: white;
}

.cart_button button a {
  text-decoration: none;
  color: white;
}

form {
    display: block;
    padding: 30px;
    margin: 0 auto;
    width: 100%;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
    border-radius: 8px;
    background-color: var(--color-primary);
}

#topform {
    width: 100%;
    margin: 4% auto;
    margin-left: 3%;
    padding: 4%;
    text-align: center;
}

form strong,#topform strong,form p {
  color: black;
}

form h2 {
    text-align: center;
    padding: 2% 0;
}

form h2,h3 {
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color:white
}

.customer_status_label {
    padding: 1.5%;
    width: 50%;
    margin: 1% auto;
    display: block;
}

form .row {
    display: flex;
}

.row input {
    margin: 1% 1%;
    margin-top: 3%;
    margin-bottom: 3%;
    padding: 2%;
    border-top: none;
    border-right: none;
    border-left: none;
}

form .label input,form button {
    width: 98%;
    padding: 10px;
    margin: 2%;
    outline: none;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 6px;
}

 /* .checkout-field {
  background-color: red;
} */

.checkout-field input,select {
  margin: 0.5% auto;
  margin-top: 5%;
  margin-left: 1%;
  margin-right: 1%;
  padding: 1%;
} 

.checkout-form .check {
  text-align: center;
  padding: 2%;
}

.checkout-form .first {
  display: block;
  position: absolute;
  left: 57%;
  padding: 0.5%;
  background-color: indianred;
  
}

.checkout-form .second {
  position: absolute;
  padding: 0.5%;
  background-color: indianred;
  margin-left: 5%;
}

form input {
    margin-bottom: 5%;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

form textarea {
    resize: vertical;
    width: 100%;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

form select {
    margin-left: 2%;
    margin-bottom: 2%;
    padding: 1%;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

form button {
    cursor: pointer;
    background-color: #1e3a8a;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
    color: white;
    animation: pulse 2s infinite;
}

.label {
    display: inline;
    margin: 2%;
    width: 15%;
    padding: 2%;
    text-align: center;
}

form span {
    color: red;
}

.second {
    display: inline-block;
    margin-left: 20%;
}

.message,.subject {
    width: 90%;
}

.Social_media_handles {
    height: auto;
    margin-top: 3%;
    margin-right: 10%;
    padding: 2% 3%;
    height: 20%;
}

.Social_media_handles a {
    display: inline-block;
    margin: 0 10%;
}

.custmoer_status {
    background-color: red;
    display: inline;
}

@keyframes pulse {
    0% {
        transform: scale(1);
        box-shadow: 0 6px 20px rgb(255,102,0,0.4);
    }
    70% {
        transform: scale(1.03);
        box-shadow: 0 0 0 10px rgb(255,102,0,0);
    }

    100% {
        transform: scale(1);
        box-shadow: 0 6px 20px rgb(255,102,0,0);
    }
}

footer {
    background-color: var(--color-primary);
    color: black;
    position: relative;
    z-index: 1;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 0;
    padding-bottom: 0.3%;
    height: auto;
}

footer .logo span {
    margin-top: 3.3%;
}

footer .logo img {
    display: inline-block;
    box-shadow: none;
    margin-top: 5%;
}
footer a {
    color: black;
}

.footer_container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.copyright-inform {
    width: 26%;
    padding: 1%;
    color: black;
    margin-left: 2%;
    margin: 1% auto;
}

.copyright-inform .inline {
    display: inline;
    width: 40%;
    padding: 5% 0;
}

.copyright-inform .inline strong {
    color: black;
}

.footer_section {
    width: 38%;
    margin: 1%;
    padding: 1% 0;
}

.footer_section:first-child {
    width: 35%;
    font-size: 15px;
    margin: 1% auto;
    padding: 1% 0;
}

.footer_section ul {
    margin-left: 4%;
    width: 90%;
    background-color: inherit;
    margin-top: 1%;
    padding-top: 1%;
    flex-wrap: wrap;
    color: black;
    margin-left: 50%;
    box-shadow: none;
}

.footer_section ul li {
    padding: 1% 2%;
    width: 35.5%;
    margin: 0.5% auto;
}

.footer_section ul .size {
    width: 40%;
}

.footer_section ul #size {
    margin-right: 5.5%;
}

.footer_section ul li:last-child {
    margin: 0;
    margin-left: 5%;
}

.footer_section ul li a {
    display: block;
    padding: 1% 1%;
}

.footer_section .strong {
    display: block;
    width: 20%;
    text-align: center;
    padding: 1%;
    position: relative;
    left: 70%;
    color: white;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

#contact {
    width: 15%;
    height: 20%;
    padding-top: 1%;
    position: absolute;
    bottom: 60%;
    margin-left: 3%;
}

.footer_section .contact {
    display: block;
    width: 8%;
    padding: 0.2%;
    position: absolute;
    margin: 0 auto;
    margin-left: 6%;
}

#contact a {
    display: block;
    width: 80%;
    text-align: center;
    padding: 2%;
    margin: 2% auto;
}

.footer_section:first-child .strong {
    margin-left: 14%;
}

.footer_section button {
    display: block;
    margin: 0 10%;
    color: black;
}

.button:hover {
    box-shadow: none
}

.footer_section button img {
    display: inline-block;
    position: relative;
    right: 35%;
}

.footer_section .app {
    background-color: inherit;
    border: none;
}

.footer_section .app {
    margin: 2% auto;
    width: 60%;
    text-align: center;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

#app {
    height: 20%;
}

#app img {
    width: 1.3%;
    position: absolute;
    left: 75.2%;
    margin: 1% 0 0 0;
    cursor: pointer;
}

#app p {
    text-align: center;
    padding: 1% 0.3%;
    width: 60%;
    margin: 2% auto;
    color: black;
}

#app section {
    width: 30%;
    margin: 1% auto;
    padding: 0.5%;
}

.footer_section a {
    display:block;
    margin-bottom:8px;
    text-decoration: none;
    color: white;
}

.footer_section .app .download a, .social a { 
    display: block;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color: white;
}

.copyright-inform .social {
    padding: 1% 1%;
    position: absolute;
    text-align: center;
    bottom: 2%;
    left: 76%;
    width: 20%;
    color: black;
}

.contact_us strong {
    display: block;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    padding: 10%;
    width: 100%;
}

footer #social {
    display: inline;
    padding: 5%;
    position: absolute;
    left: 60%;
    top: 15%;
}

.footer_container .social {
    position: absolute;
    left: 80%;
    padding: 1%;
    margin-top: 1%;
}

.footer_container .social a {
    display: block;
    width: 20%;
    margin: 2% auto;
    padding: 5%;
    font-size: large;
    text-align: center;
}

