    width: auto;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    color: white;
} */

.copyright-inform .social a {
    display: inline;
    width: 20%;
    margin: 2% 2%;
    padding: 1%;
}
